<template>
    <v-ons-page id="post-form">
        <div class="bg">
            <div class="bg-over">
            <h1 style="text-align: center">リア充っぽい投稿</h1>
            <form style="padding: 0 20px">
                <div class="row">
                    <label for="todo">やったリア充っぽいこと</label><br>
                    <v-ons-select v-model="selectTodo" id="todo">
                        <option v-for="todo in todos" :value="todo.id">
                            {{ todo.name }}
                        </option>
                    </v-ons-select>
                </div>
                <div class="row" id="input-image">
                    <label for="">リア充っぽい写真</label><br>
                    <div class="display-image">
                        <img :src="imageSrc" alt="">
                    </div>
                    <label for="select-image" id="select-image-label">写真を選ぶ</label>
                    <input type="file" id="select-image" @change="selectImage($event)">
                </div>
                <div style="margin-top: 40px">
                    <button type="button" id="post-button" @click="post">投稿</button>
                </div>
            </form>
            <v-ons-modal :visible="postingWait" id="post-wait">
                <p style="text-align: center">
                    投稿中 <v-ons-icon icon="fa-spinner" spin></v-ons-icon>
                </p>
            </v-ons-modal>
            <v-ons-modal :visible="postResultShow" :options="{ animation: 'fade' }" id="result-modal">
                <post-result :result="postResult"></post-result>
                <button type="button" id="close-result-button" @click="postResultShow = false">閉じる</button>
            </v-ons-modal>
            </div>
        </div>
    </v-ons-page>
</template>

<script>
import PageMixin from './PageMixin'
import Page from './index'
import PostResult from '../components/PostResult.vue'
export default {
    mixins: [PageMixin],
    data() {
        return {
            selectTodo: {},
            todos: [
                {
                    "id": 1,
                    "name": "花火大会",
                    "base_point": "300"
                },
                {
                    "id": 2,
                    "name": "手持ち花火",
                    "base_point": "100"
                },
                {
                    "id": 3,
                    "name": "夏祭り",
                    "base_point": "300"
                },
                {
                    "id": 4,
                    "name": "虫取り",
                    "base_point": "50"
                },
                {
                    "id": 5,
                    "name": "キャンプ",
                    "base_point": "300"
                },
                {
                    "id": 6,
                    "name": "キャンプファイヤー",
                    "base_point": "700"
                },
                {
                    "id": 7,
                    "name": "BBQ",
                    "base_point": "100"
                },
                {
                    "id": 8,
                    "name": "肝試し",
                    "base_point": "100"
                },
                {
                    "id": 9,
                    "name": "プール",
                    "base_point": "50"
                },
                {
                    "id": 10,
                    "name": "ナイトプール",
                    "base_point": "1000"
                },
                {
                    "id": 11,
                    "name": "海水浴",
                    "base_point": "150"
                },
                {
                    "id": 12,
                    "name": "ビーチバレー",
                    "base_point": "100"
                },
                {
                    "id": 13,
                    "name": "サーフィン",
                    "base_point": "100"
                },
                {
                    "id": 14,
                    "name": "川遊び",
                    "base_point": "150"
                },
                {
                    "id": 15,
                    "name": "スイカ割り",
                    "base_point": "400"
                },
                {
                    "id": 16,
                    "name": "夏フェス",
                    "base_point": "300"
                },
                {
                    "id": 17,
                    "name": "流しそうめん",
                    "base_point": "100"
                },
                {
                    "id": 18,
                    "name": "天体観測",
                    "base_point": "100"
                }
            ],
            imageSrc: 'https://placehold.jp/cccccc/989898/240x200.png?text=No%20Image',
            postingWait: false,
            postResultShow: true,
            postResult: {
                "image_path": "https://real-juice.s3-ap-northeast-1.amazonaws.com/posts-1503450265.png",
                "point": "1000",
                "todo": {
                    "name": "ナイトプール",
                    "base_point": "1000"
                },
                "total_point": 1240,
                "getBonuses": [
                    {
                        "name": "異性と一緒ボーナス",
                        "point": "200"
                    },
                    {
                        "name": "アウトドアボーナス",
                        "point": "10"
                    },
                    {
                        "name": "青春ボーナス",
                        "point": "30"
                    }
                ],
                "getTitle": [
                    {
                        "name": "リア充への第一歩",
                        "point": "100"
                    }
                ]
            }
        }
    },
    mounted() {
        let items = 100

        for (var i=0; i<=items; i++) {
            var move = Math.ceil( Math.random()*50 );
            var pos = Math.ceil( Math.random()*50 );
            var scale = Math.ceil( Math.random()*10 );
            var stretch = Math.ceil( Math.random()*5 );
            var shake = Math.ceil( Math.random()*5 );
            $(this.$el).find('#post-wait').append('<div class="bubble move'+move+' pos'+pos+'"><div class="scale'+scale+'"><div class="shake'+shake+'"><span class="item stretch'+stretch+'"></span></div></div>')
        }
    },
    methods: {
        selectImage($event) {
            let file = $event.target.files[0]
            let fileReader = new FileReader()
            fileReader.onload = evt => {
                this.imageSrc = evt.target.result
            }
            fileReader.readAsDataURL(file)
        },
        post() {
            this.postingWait = true
            setTimeout(() => {
                this.postingWait = false
                this.postResultShow = true
            }, 5000)
        }
    },
    components: {
        PostResult
    }
}
</script>

<style lang="scss">
    .bg {
        width: 100%;
        height: 100%;
        background-image: url(https://s3-ap-northeast-1.amazonaws.com/real-juice/resource/back-image.jpg);
        background-size: cover;
    }
    .bg-over {
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.6);
    }
    .bg :before {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        border-right: 30px solid rgba(0, 0, 0, 0);
        border-top: 30px solid #f90;
        content: "";
    }
    .bg :after {
        display: block;
        position: absolute;
        bottom: 0;
        right: 0;
        border-left: 30px solid rgba(0, 0, 0, 0);
        border-bottom: 30px solid #f90;
        content: "";
    }
    .row {
        margin-bottom: 20px;
    }
    #todo {
        width: 100%;
    }
    #input-image {
        min-height: 300px;
    }
    #input-image img {
        width: 100%;
        height: auto;
    }
    .display-image {
        width: 100%;
        margin-bottom: 10px;
    }
    .display-image img {
        width: 100%;
        height: auto;
    }
    #select-image {
        display: none;
    }
    #select-image-label {
        display: inline-block;
        width: 100px;
        text-align: center;
        background-color: #f90;
        color: #fff;
        border-radius: 10px;
        padding: 8px;
    }
    #post-button {
        width: 100%;
        border: none;
        background-color: #4169E1;
        color: #fff;
        padding: 8px;
        border-radius: 10px;
        font-size: 24px;
    }

    #result-modal {
        padding: 20px;
    }

    #close-result-button {
        margin-top: 20px;
        border: none;
        width: 100px;
        text-align: center;
        background-color: #f90;
        color: #fff;
        border-radius: 10px;
        padding: 8px;
        font-size: 16px;
    }

    $b_white: rgba(255, 255, 255, 0.6);
    $b_blue: rgba(194, 227, 255, 1);
    $b_ease: cubic-bezier(0.470,0.000,0.745,0.715);


    //stretch setting
    @keyframes stretch {
        0% { transform: scaleX(1) scaleY(1); }
        33% { transform: scaleX(0.9) scaleY(1); }
        66% { transform: scaleX(1) scaleY(0.9); }
        100% { transform: scaleX(1) scaleY(1); }
    }
    @-webkit-keyframes stretch {
        0% { -webkit-transform: scaleX(1) scaleY(1); }
        33% { -webkit-transform: scaleX(0.9) scaleY(1); }
        66% { -webkit-transform: scaleX(1) scaleY(0.9); }
        100% { -webkit-transform: scaleX(1) scaleY(1); }
    }

    //shake setting
    @keyframes shake {
        0% { transform: translateX(10px); }
        50% { transform: translateX(-10px); }
        100% { transform: translateX(10px); }
    }
    @-webkit-keyframes shake {
        0% { -webkit-transform: translateX(10px); }
        50% { -webkit-transform: translateX(-10px); }
        100% { -webkit-transform: translateX(10px); }
    }

    //move setting
    @keyframes move {
        0% { transform: translateY(0px); }
        100% { transform: translateY(-1000px); opacity: 0; }
    }
    @-webkit-keyframes move {
        0% { -webkit-transform: translateY(0px); }
        100% { -webkit-transform: translateY(-1000px); opacity: 0; }
    }

    // ----------------------------------------------------------------------------------------
    //
    // bubble
    //
    // ----------------------------------------------------------------------------------------

       .field {
           background: -webkit-linear-gradient(top, #a5d4ea 0%,#86c6e0 100%);
           background: linear-gradient(to bottom, #a5d4ea 0%,#86c6e0 100%);
           overflow: hidden;
           position: relative;
           width: 100%;
       }

    // --------------------------------------------------------------------------------
    // item
    // --------------------------------------------------------------------------------
       .bubble {
           position: absolute;
           bottom: -20%;
           left: 50%;
    .item {
        display: block;
        width: 20px;
        height: 20px;
        border-radius: 100%;
        box-shadow: 0px 0px 15px 0px $b_white inset;
        -webkit-box-shadow: 0px 0px 15px 0px $b_white inset;
        position: relative;
    &:after {
         content: "";
         display: block;
         width: 20%;
         height: 20%;
         border-radius: 100%;
         background: rgba(255, 255, 255, 0.6);
         position: absolute;
         right: 15%;
         top: 15%;
         transform: rotateZ(45deg) scaleY(0.8);
         -webkit-transform:rotateZ(45deg) scaleY(0.8) ;
     }
    }
    }

    @for $i from 0 through 50 {
    $time: $i*0.2;
    $scale: $i*0.1;
        .stretch#{$i * 1} {
            animation: stretch ($time+2)+s ease 0s infinite normal;
            -webkit-animation: stretch ($time+2)+s ease 0s infinite normal;
        }
        .shake#{$i * 1} {
            animation: shake ($time+2)+s ease 0s infinite normal;
            -webkit-animation: shake ($time+2)+s ease 0s infinite normal;
        }
        .move#{$i * 1}  {
            animation: move ($time+5)+s $b_ease $time+s infinite normal;
            -webkit-animation: move ($time+5)+s $b_ease $time+s infinite normal;
        }
        .pos#{$i * 1}  {
            left: percentage($i*2/100);
        }
        .scale#{$i * 1}  {
            transform: scale($scale);
            -webkit-transform: scale($scale);
        }
    }
</style>



